function loadScript(src, callback) {
   var script = document.createElement('script'),
  head = document.getElementsByTagName('head')[0];
  script.type = 'text/javascript';
  script.charset = 'UTF-8';
  script.src = src;
  if (script.addEventListener) {
    script.addEventListener('load', function () {
      callback();
    }, false);
  } else if (script.attachEvent) {
    script.attachEvent('onreadystatechange', function () {
      var target = window.event.srcElement;
      if (target.readyState == 'loaded') {
        callback();
      }
    });
  }
  head.appendChild(script);
}
const setQmlPlayerProperty =(param)=>{
  vm.$sumaQtPlayer.setPlayerProperty(JSON.stringify(param), function (
    callback
  ) {
  });
}
loadScript('../static/qwebchannel.js',function(){
  console.log('onload');
  let sumaQtPlayer
  new QWebChannel(qt.webChannelTransport, function (channel) {
    sumaQtPlayer = channel.objects.sumaQtPlayer;
    console.log('66',sumaQtPlayer)
    Vue.prototype.$sumaQtPlayer = sumaQtPlayer
    vm.$sumaQtPlayer.playerStatusChangeSignal.connect(function (paramJson) {
      let obj = JSON.parse(paramJson)
     if(obj.propertyChangeSource === 'player'){
       //
       vm.receiveObj = obj /**player */
       vm.$bus.$emit('receiveObj',obj)
       if(obj.propertyName === 'isRecording'){
         if(!obj.channelId){
           //
           let recodeObj = [{
             groupIndex:obj.groupIndex,
             playerIndex:obj.playerIndex,
             channelId:obj.channelId,
             business:{
               isRecording:false,
               isDotting:false
             }
           }]
           vm.$Message.info(',')
           setQmlPlayerProperty(recodeObj)
           return false
         }
         if(obj.newValue==='true'){
           //
           vm.recodeSwitchFlag = true /**player */
           vm.$bus.$emit('recodeSwitchFlag',true)
           //==
           let arr = JSON.parse(sessionStorage.getItem('monitorState'))
           let reArr=[]
           for(let item of arr){
             if(item.playerIndex !== obj.playerIndex){
               reArr.push(item)
             }
           }
           setQmlPlayerProperty(reArr)
         }else{
           vm.recodeSwitchFlag = false /**player */
           vm.$bus.$emit('recodeSwitchFlag',false)
           //========
           let arr = JSON.parse(sessionStorage.getItem('monitorState'))
           let reArr=[]
           for(let item of arr){
             if(item.playerIndex !== obj.playerIndex){
               reArr.push(item)
             }
           }
           setQmlPlayerProperty(reArr)
         }
         vm.recodePlanConfirmModal = true
         vm.$bus.$emit('showRecodeMode')
          let param = [{
             groupIndex:-2,
             playerIndex:-2,
             channelId:'',
             business:{},
             player:{
               isVisible:false
             }
           }]
         //
          setQmlPlayerProperty(param)
       }
       if(obj.propertyName === 'isDotting'){
         if(!obj.channelId){
           //
            let recodeObj = [{
             groupIndex:obj.groupIndex,
             playerIndex:obj.playerIndex,
             channelId:obj.channelId,
             business:{
               isRecording:false,
               isDotting:false
             }
           }]
           vm.$Message.info(',')
           setQmlPlayerProperty(recodeObj)
           return false
         }

         let arr = JSON.parse(sessionStorage.getItem('monitorState'))
         for(let item of arr){
           if(item.groupIndex === obj.groupIndex && item.playerIndex === obj.playerIndex){
             if(item.business.isRecording === false){
               let recodeObj = [{
                 groupIndex:obj.groupIndex,
                 playerIndex:obj.playerIndex,
                 channelId:obj.channelId,
                 business:{
                   isRecording:false,
                   isDotting:false
                 }
               }]
                setQmlPlayerProperty(recodeObj)
                vm.$Message.info(',')
                return false
             }
             break
           }
         }
         if(obj.newValue==='true'){
           //
           console.log('vm',vm)
           let arr = JSON.parse(sessionStorage.getItem('monitorState'))
           for(let item of arr){
             /** 2321bug  */
             let isGray = ((JSON.parse(localStorage.getItem('loginRes'))).user.operationPermissions) == 1 ? false:true
             if (isGray === true) {
               if (item.playerIndex === obj.playerIndex) {
                 let recodeObj = [{
                   groupIndex:item.groupIndex,
                   playerIndex:item.playerIndex,
                   channelId:item.channelId,
                   business:{
                     isRecording:item.business.isRecording,
                     isDotting:false
                   }
                 }]
                 setQmlPlayerProperty(recodeObj)
                 vm.$Message.info('');
                 return false;
               } else {
                 //
                 if (item.channelId === obj.channelId) {
                   let recodeObj1 = [{
                     groupIndex:obj.groupIndex,
                     playerIndex:obj.playerIndex,
                     channelId:item.channelId,
                     business:{
                       isRecording:item.business.isRecording,
                       isDotting:false
                     }
                   }]
                   setQmlPlayerProperty(recodeObj1)
                   vm.$Message.info('');
                   return false;
                 }
               }
             }
             if(item.playerIndex === obj.playerIndex){
                item.business.isDotting = true
                item.timestamp = obj.timestamp //
             }else{
               //
               if(item.channelId === obj.channelId){
                 item.business.isDotting = true
                 item.timestamp = obj.timestamp
               }
             }
           }
           //===
           setQmlPlayerProperty(arr)
           sessionStorage.setItem('monitorState',JSON.stringify(arr))
         }else{
           //====================
           let arr = JSON.parse(sessionStorage.getItem('monitorState'))
           let reArr=[]
           for(let item of arr){
             if(item.playerIndex !== obj.playerIndex){
               reArr.push(item)
             }
           }
           setQmlPlayerProperty(reArr)
           //=====================
           vm.dottingModal = true
           vm.$bus.$emit('dottingModal',true)
         }
       }
       //===================
       if(obj.propertyName === 'isFullArea'){
         /**groupIndex */
         if(obj.newValue === 'true'){
            if(obj.channelId){
              // vm.$emit('fullArea',obj)
              vm.$bus.$emit('fullArea',obj)
            }else{
              vm.$Message.info(',')
            }
          }else{
            if(obj.channelId){
              //vm.$emit('cancelFullArea',obj) ///==========================
              vm.$bus.$emit('cancelFullArea',obj)
            }
          }
       }
       //====================
       if(obj.propertyName === 'isCreate'){
         let playerType = sessionStorage.getItem('playerType')
         if(obj.newValue === 'true'){
          if(playerType==='base'){
            // vm.$emit('isBaseCreate',obj)
            vm.$bus.$emit('isBaseCreate',obj)
            // vm.playerbaseIndex = obj.groupIndex
          }else{
            // vm.$emit('isRecCreate',obj)
            vm.$bus.$emit('isRecCreate',obj)
            // vm.playerRecIndex = obj.groupIndex
          }
         }
       }
       //
       if(obj.propertyName ==="isEnd"){
         if(vm.playerType==='rec'){
           vm.$emit('isRecEnd',obj)
         }else{
           vm.$emit('isEnd',obj.groupIndex)
         }
       }
       //===================
       if(obj.propertyName === 'isFullScreen'){
         if(obj.newValue === 'true'){
            vm.$emit('fullScreen',obj)
          }else{
            vm.$emit('cancelFullScreen',obj)
          }
       }
     }
   });
  })
});

